$prime: #00E5FF;
$second: #021111;
$secondLight: #042121;

body,
html {
    align-items: center;
    background-color: $second;
    display: flex;
    font-family: 'Roboto';
    font-size: 10px;
    height: 100%;
    justify-content: center;
    margin: 0;
    padding: 0;
    width: 100%; }

* {
    box-sizing: border-box; }

